import { API } from '@/shared/config'
import axios from 'axios'
import requestInterceptor from '@/shared/requestInterceptor'
import { parseAxiosErrorToAppError } from '@/shared/errorHelper'

async function getParksList () {
  try {
    const response = await axios.get(`${API}/parks`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getTrailsFromPark (parkId) {
  try {
    const response = await axios.get(`${API}/trails?parkId=${parkId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getTrailSegment (segmentId) {
  try {
    const response = await axios.get(`${API}/segments/${segmentId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function getTrailLikes (trailId) {
  try {
    const response = await axios.get(`${API}/trails/${trailId}/likes`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function postTrailLike (trailId, userId) {
  try {
    const response = await requestInterceptor.post(`${API}/likes`, {
      trailId: trailId,
      userId: userId
    })
    console.log(response)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

async function deleteTrailLike (likeId) {
  try {
    const response = await requestInterceptor.delete(`${API}/likes/${likeId}`)
    return response.data
  } catch (error) {
    throw parseAxiosErrorToAppError(error)
  }
}

export const trailService = {
  getParksList,
  getTrailsFromPark,
  getTrailSegment,
  getTrailLikes,
  postTrailLike,
  deleteTrailLike
}
