import { trailService } from '@/services/trailService.js'

const state = {
  parks: [],
  trails: [],
  onError: false,
  selectedTrail: '',
  segments: [],
  trailLikesList: [],
  likesCount: 0,
  userId: null
}

const getters = {
  getAlreadyLiked: state => {
    const trailLikesList = state.trailLikesList
    for (var i = 0; i < trailLikesList.length; i++) {
      if (trailLikesList[i].userId === state.userId) {
        return true
      }
    }

    return false
  }
}

const mutations = {
  initializeParks: (state, parks) => {
    state.parks = parks
  },
  initializeTrails: (state, trails) => {
    state.trails = trails
  },
  setOnError (state) {
    state.onError = true
  },
  setSelectedTrail: (state, trail) => {
    state.selectedTrail = trail
  },
  setSegments: (state, segments) => {
    state.segments = segments
  },
  setLikesList: (state, trailsLikes) => {
    state.trailLikesList = trailsLikes
  },
  setLikesCount: state => {
    state.likesCount = state.trailLikesList.length
  },
  setUserId: (state, userId) => {
    state.userId = userId
  }
}

const actions = {
  async getAllParksAction ({ commit }) {
    try {
      const parks = await trailService.getParksList()
      commit('initializeParks', parks)
    } catch (error) {
      commit('setOnError')
    }
  },
  async getTrailsFromParkAction ({ commit }, park) {
    try {
      const trails = await trailService.getTrailsFromPark(park.id)
      commit('initializeTrails', trails)
    } catch (error) {
      commit('setOnError')
    }
  },
  async getTrailSegmentsAction ({ commit }) {
    const segments = []

    try {
      for (const segment of state.selectedTrail.segments) {
        const seg = await trailService.getTrailSegment(segment)
        segments.push(seg)
      }
      commit('setSegments', segments)
    } catch (error) {
      commit('setOnError')
      console.log(error)
    }
  },
  async getTrailLikesAction ({ commit }) {
    try {
      const likes = await trailService.getTrailLikes(state.selectedTrail.id)
      commit('setLikesList', likes)
      commit('setLikesCount', likes)
    } catch (error) {
      commit('setOnError')
      console.log(error)
    }
  },
  async postTrailLikesAction ({ commit }) {
    try {
      await trailService.postTrailLike(state.selectedTrail.id, state.userId)
    } catch (error) {
      commit('setOnError')
      console.log(error)
    }
  },

  async deleteTrailLikeAction ({ commit }) {
    try {
      const likes = await trailService.getTrailLikes(state.selectedTrail.id)
      for (const like of likes) {
        if (like.userId === state.userId) {
          await trailService.deleteTrailLike(like.id)
        }
      }
    } catch (error) {
      commit('setOnError')
      console.log(error)
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
