<template>
  <div>
    <b-button
      v-if="liked"
      size="lg"
      variant="light"
      class="mb-2"
      @click="dislikeTrail"
    >
      <b-icon icon="heart-fill" variant="danger"></b-icon>
    </b-button>

    <b-button
      v-if="!liked"
      size="lg"
      variant="light"
      class="mb-2"
      @click="likeTrail"
    >
      <b-icon icon="heart" variant="danger"></b-icon>
    </b-button>
    <p style="background-color: pink">{{ likesCount }}</p>
  </div>
</template>

<script>
export default {
  computed: {
    likesCount () {
      return this.$store.state.trails.likesCount
    },
    liked () {
      if (this.isLoggedIn) {
        return this.$store.getters['trails/getAlreadyLiked']
      } else return false
    },
    isLoggedIn () {
      return this.$store.getters['authentication/isLoggedIn']
    }
  },
  methods: {
    async likeTrail () {
      if (this.isLoggedIn) {
        this.liked = true
        await this.$store.dispatch('trails/postTrailLikesAction')
        await this.getLikes()
      } else {
        this.$router.push({
          name: 'Login'
        })
      }
    },
    async dislikeTrail () {
      if (this.isLoggedIn) {
        this.liked = false
        await this.$store.dispatch('trails/deleteTrailLikeAction')
        await this.getLikes()
      }
    },

    async getLikes () {
      if (this.$store.state.trails.selectedTrail !== '') {
        if (this.isLoggedIn) {
          const userId = this.$store.getters['authentication/getTokenUserId']
          this.$store.commit('trails/setUserId', userId)
        }

        await this.$store.dispatch('trails/getTrailLikesAction')
      }
    }
  }
}
</script>

<style lang="scss" scoped></style>
