<template>
  <div class="d-flex">
    <b-container>
      <b-row>
        <b-col align-self="baseline">
          <div class="pt-3 pb-3">
            <b-form-select class="w-200 parksListSelect" v-model="selectedPark">
              <b-form-select-option :value="''" selected disabled
                >Choisissez un parc</b-form-select-option
              >
              <b-form-select-option
                v-for="park in parkList"
                v-bind:key="park.id"
                v-bind:value="park"
                >{{ park.name }}</b-form-select-option
              ></b-form-select
            >
          </div>

          <b-form-select
            class="trailsListSelect"
            v-model="selectedTrail"
            v-if="trailList.length"
          >
            <b-form-select-option
              v-for="trail in trailList"
              variant="primary"
              v-bind:key="trail.id"
              v-bind:value="trail"
            >
              {{ trail.name }}</b-form-select-option
            >
          </b-form-select>
        </b-col>
        <Like ref="likes" v-if="selectedTrail != ''" />
        <Map />
      </b-row>
    </b-container>
  </div>
</template>

<script>
import Map from '@/components/Map.vue'
import Like from '@/components/Like.vue'
export default {
  data () {
    return {
      parkList: [],
      trailList: [],
      selectedPark: '',
      selectedTrail: ''
    }
  },
  components: {
    Map,
    Like
  },

  async created () {
    await this.loadParks()
  },

  methods: {
    async loadParks () {
      await this.$store.dispatch('trails/getAllParksAction')
      this.parkList = this.$store.state.trails.parks
    },

    async loadTrailsFromSelectedPark () {
      await this.$store.dispatch(
        'trails/getTrailsFromParkAction',
        this.selectedPark
      )
      this.trailList = this.$store.state.trails.trails
    },
    async setSelectedTrail () {
      this.$store.commit('trails/setSelectedTrail', this.selectedTrail)
      await this.$store.dispatch('trails/getTrailSegmentsAction')
    }
  },

  watch: {
    selectedPark: async function () {
      await this.loadTrailsFromSelectedPark()
    },
    selectedTrail: async function () {
      await this.setSelectedTrail()
      await this.$refs.likes.getLikes()
    }
  }
}
</script>

<style lang="scss" scoped></style>
