<template>
  <div>
    <h1 class="text-white bg-success">{{ trailName }}</h1>
    <l-map style="height: 600px; width: 920px" :zoom="zoom" :center="center">
      <l-tile-layer :url="url" :attribution="attribution"></l-tile-layer>
      <l-marker :lat-lng="markerLatLng"></l-marker>
      <l-polyline
        v-for="segment in segments"
        v-bind:key="segment"
        :lat-lngs="segment.coordinates"
        :color="setColor(segment.level)"
      ></l-polyline>
    </l-map>
  </div>
</template>

<script>
import { LMap, LTileLayer, LMarker, LPolyline } from 'vue2-leaflet'
// import 'leaflet/dist/leaflet.css'
export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPolyline
  },
  data () {
    return {
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      attribution:
        '&copy; <a target="_blank" href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      zoom: 15
    }
  },
  computed: {
    segments () {
      return this.$store.state.trails.segments
    },
    center () {
      if (this.$store.state.trails.segments.length !== 0) {
        return this.$store.state.trails.segments[0].coordinates[0]
      } else {
        return [46.71, -71.292]
      }
    },
    markerLatLng () {
      if (this.$store.state.trails.segments.length !== 0) {
        return this.$store.state.trails.segments[0].coordinates[0]
      } else {
        return [46.71, -71.292]
      }
    },
    trailName () {
      return this.$store.state.trails.selectedTrail.name
    }
  },
  methods: {
    setColor (difficulty) {
      if (difficulty === 'Très difficile') return 'red'

      if (difficulty === 'Difficile') return 'orange'

      if (difficulty === 'Intermédiaire') return 'blue'

      if (difficulty === 'Facile') return 'green'
      else return 'black'
    }
  }
}
</script>

<style lang="scss" scoped></style>
