<template>
  <div class="">
    <b-tabs pills content-class="mt-3" align="right">
      <b-tab title="Sentiers" active><Trails /></b-tab>
      <div v-if="!isLoggedIn" class="">
        <b-tab title="Se Connecter"><Login /></b-tab>
        <b-tab title="S'inscrire"><Register /> </b-tab>
      </div>
      <div v-else>
        <b-tab title="Se Déconnecter" @click="logout"></b-tab>
      </div>
    </b-tabs>
  </div>
</template>

<script>
import Trails from '@/components/Trails.vue'
import Login from '@/components/Login.vue'
import Register from '@/components/Register.vue'
export default {
  name: 'Home',
  props: {
    msg: String
  },
  components: {
    Login,
    Register,
    Trails
  },
  computed: {
    isLoggedIn () {
      return this.$store.getters['authentication/isLoggedIn']
    }
  },
  methods: {
    logout () {
      this.$store.dispatch('authentication/logout')
      this.$router.push({
        name: 'Login'
      })
    }
  }
}
</script>

